<template>
  <div class="rig" v-loading.fullscreen.lock="loading">
    <el-row style="margin-top: 20px; margin-bottom: 20px;">
      <el-col :span="12">
        <el-breadcrumb separator-class="mdi mdi-chevron-right">
          <el-breadcrumb-item :to="{path: '/dashboard'}">Home</el-breadcrumb-item>
          <el-breadcrumb-item :to="{path: '/farm/' + this.$route.params.id}">{{farm.name}}</el-breadcrumb-item>
          <el-breadcrumb-item>Rig #{{farm.workers?.id}} ({{farm.workers?.name || 'undefined'}})</el-breadcrumb-item>
        </el-breadcrumb>
      </el-col>
      <el-col :span="12" class="rig-commands-col" v-if="farm.workers.hardwares !== undefined">
        <el-space>
          <el-dropdown trigger="click" placement="bottom">
            <el-button><span class="mdi mdi-power"></span></el-button>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item @click="reboot">Reboot</el-dropdown-item>
                <el-dropdown-item @click="shutdown">Shutdown</el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
          <el-dropdown trigger="click" placement="bottom">
            <el-button><span class="mdi mdi-pickaxe"></span></el-button>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item>Stop miner</el-dropdown-item>
                <el-dropdown-item>Restart miner</el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
          <el-dropdown trigger="click" placement="bottom" :hide-on-click="false">
            <el-button><span class="mdi mdi-fan"></span></el-button>
            <template #dropdown>
              <el-dropdown-menu style="width: 30em; padding: 10px!important;">
                <el-row>
                  <el-col :span="12">
                    <h4>Critical temperature</h4>
                  </el-col>
                  <el-col :span="12">
                    <el-input v-model="autofan.criticalTemp" type="number" suffix-icon="mdi mdi-temperature-celsius"/>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="12">
                    <h4>Critical action</h4>
                  </el-col>
                  <el-col :span="12">
                    <el-select v-model="autofan.criticalAction">
                      <el-option label="Stop miner" value="stop"/>
                      <el-option label="Reboot" value="reboot"/>
                      <el-option label="Shutdown" value="shutdown"/>
                    </el-select>
                  </el-col>
                </el-row>
                <el-row style="align-items: center;">
                  <el-col :span="12">
                    <h4>Enable Autofan</h4>
                  </el-col>
                  <el-col :span="12">
                    <el-switch v-model="autofan.enabled"/>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="12">
                    <h4>Target core TEMP</h4>
                  </el-col>
                  <el-col :span="12">
                    <el-input v-model="autofan.targetCoreTemp" type="number" suffix-icon="mdi mdi-temperature-celsius"/>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="12">
                    <h4>Target MEM Temp</h4>
                  </el-col>
                  <el-col :span="12">
                    <el-input v-model="autofan.targetMemTemp" type="number" suffix-icon="mdi mdi-temperature-celsius"/>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="12">
                    <h4>Min fan speed</h4>
                  </el-col>
                  <el-col :span="12">
                    <el-input v-model="autofan.minFanSpeed" type="number" suffix-icon="mdi mdi-percent"/>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="12">
                    <h4>Max fan speed</h4>
                  </el-col>
                  <el-col :span="12">
                    <el-input v-model="autofan.maxFanSpeed" type="number" suffix-icon="mdi mdi-percent"/>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="12">
                    <h4>No AMD</h4>
                  </el-col>
                  <el-col :span="12">
                    <el-switch v-model="autofan.noAmd"/>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="12">
                    <h4>Smart mode</h4>
                  </el-col>
                  <el-col :span="12">
                    <el-switch v-model="autofan.smartMode"/>
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="12">
                    <h4>Reboot on errors</h4>
                  </el-col>
                  <el-col :span="12">
                    <el-switch v-model="autofan.rebootOnError"/>
                  </el-col>
                </el-row>
                <div style="float:right; margin-bottom: 10px;">
                  <el-button type="primary" size="mini" :loading="applyAutofanLoading" @click="applyAutofan">Apply</el-button>
                </div>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
          <el-button @click="runCommandVisible = true"><span class="mdi mdi-console-line"></span></el-button>
        </el-space>
      </el-col>
    </el-row>
    <el-dialog title="Run a command" v-model="runCommandVisible">
      <el-row>
        <el-col :span="22">
          <el-input v-model="commandToRun" placeholder="Type a command here"/>
        </el-col>
        <el-col :span="2">
          <el-button type="primary" :disabled="commandToRun === ''" :loading="runCommandLoading" plain>Send</el-button>
        </el-col>
      </el-row>
    </el-dialog>
    <el-empty description="Add your worker to see its informations" v-if="farm.workers.hardwares === undefined"/>
    <div v-else class="rig-header">
      <el-row>
        <el-col>
          <h2>Rig #{{farm.workers.id}} ({{farm.workers.name}})</h2>
        </el-col>
      </el-row>
      <el-row v-if="!isOnline">
        <el-col>
          <p style="color: var(--el-color-danger); font-size: 1.1rem; margin: 0;"><span class="mdi mdi-wifi-off"></span> Offline</p>
        </el-col>
      </el-row>
      <el-row>
        <el-col>
          <el-space wrap spacer="|" :size="12">
            <p class="gray-bold">BOOTED <span class="gray-normal">{{formatTimestamp}}</span></p>
            <p class="gray-bold">IP <span class="gray-normal">{{farm.workers.hardwares?.lan_config.address}}</span></p>
            <p class="gray-bold">VERSION <span class="gray-normal">{{farm.workers.hardwares?.version}}</span></p>
          </el-space>
        </el-col>
      </el-row>
      <el-row class="rig-row-miner">
        <el-col :span="12" style="text-align: left;">
          <el-row>
            <el-col class="rig-row-miner-title">
              {{flightsheets.inuse[0]?.miner.name}} <span class="rig-miner-version">{{farm.workers.stats?.miner_stats?.ver}}</span>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="4">
              {{flightsheets.inuse[0]?.coin}}
            </el-col>
            <el-col :span="10">
              {{flightsheets.inuse[0]?.name}}
            </el-col>
            <el-col :span="10">
              {{flightsheets.inuse[0]?.pool.name}}
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="3">
          <el-row>
            <el-col class="rig-row-algo-title">
              {{farm.workers.stats?.miner_stats?.algo}}
            </el-col>
          </el-row>
          <el-row>
            <el-col class="rig-row-algo-hs">
              {{hasrateFromRaw(farm.workers.stats?.miner_stats?.total_khs)}}
            </el-col>
          </el-row>
        </el-col>
        <el-col :span="9">
          <el-row>
            <el-col :span="24/farm.workers.hardwares?.gpu.length" v-for="index in farm.workers.stats?.miner_stats?.hs.length"
              :key="index">
              <el-card class="rig-gpu-info-card">
                <div style="text-align: right;">{{farm.workers.stats?.miner_stats?.temp[index-1]}}°</div>
                <div style="background-color: deepskyblue; padding-left: 10px; text-align: right;">{{farm.workers.stats?.miner_stats?.fan[index-1]}}%</div>
                <div>{{this.hasrateFromRaw(farm.workers.stats?.miner_stats?.hs[index-1])}}</div>
              </el-card>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
    </div>
    <el-row v-if="farm.workers.hardwares !== undefined">
      <el-col>
        <el-tabs class="rig-tabs" v-model="activeTab">
          <el-tab-pane class="rig-tabs-pane" label="Overview" name="overview">
            <el-row>
              <el-col>
                <el-card>
                  <el-table :data="farm.workers.hardwares?.gpu" stripe>
                    <el-table-column label="GPU">
                      <template #default="scope">
                        <div class="gpu-table-name">{{scope.row.name}}</div>
                        <div class="gpu-table-desc">{{scope.row.subvendor}} &#8226; {{scope.row.mem_type}}</div>
                      </template>
                    </el-table-column>
                    <el-table-column label="Hashrate">
                      <template #default="scope">
                        {{hashrateFromGPU(scope.$index)}}
                      </template>
                    </el-table-column>
                    <el-table-column prop="mem" label="MEM"/>
                    <el-table-column label="TEMP">
                      <template #default="scope">
                        {{farm.workers.stats?.miner_stats?.temp[scope.$index]}}°C
                      </template>
                    </el-table-column>
                    <el-table-column label="WATT">
                      <template #default="scope">
                        {{farm.workers.stats?.power[scope.$index]}}W
                      </template>
                    </el-table-column>
                  </el-table>
                </el-card>
              </el-col>
            </el-row>
            <el-row style="margin-top: 20px;" justify="center">
              <el-col :span="3" :xs="24" class="rig-col-cards">
                <el-card class="rig-cards">
                  <h4>Consumption</h4>
                  <p style="font-size: 1.4rem;">{{totalConsumption}}W <span class="mdi mdi-lightning-bolt-circle" style="color: var(--el-color-warning);"></span></p>
                </el-card>
              </el-col>
              <el-col :span="3" :xs="24" class="rig-col-cards">
                <el-card class="rig-cards">
                  <h4>OS version</h4>
                  <p style="font-size: 1.4rem;">{{farm.workers.hardwares?.version}}</p>
                </el-card>
              </el-col>
              <el-col :span="3" :xs="24" class="rig-col-cards">
                <el-card class="rig-cards">
                  <h4>Drivers</h4>
                  <p style="font-size: 0.8rem;"><span style="color: blueviolet;">AMD</span>: {{farm.workers.hardwares?.amd_version}}</p>
                  <p style="font-size: 0.8rem;"><span style="color: yellowgreen;">NVIDIA</span>: {{farm.workers.hardwares?.nvidia_version}}</p>
                </el-card>
              </el-col>
            </el-row>
          </el-tab-pane>
          <el-tab-pane class="rig-tabs-pane" label="Flight Sheet" name="flightsheet">
            <el-row>
              <el-col>
                <h3>In-use</h3>
              </el-col>
            </el-row>
            <el-row>
              <el-col>
                <Flightsheet :flightsheets="flightsheets.inuse" nonew nodelete>
                  <template #action>
                    <el-tooltip effect="dark" content="Stop flightsheet" placement="top">
                      <el-button type="danger" :loading="flightsheets.launched" @click.stop="stopFlightsheet">
                        <span class="mdi mdi-stop-circle"></span>
                      </el-button>
                    </el-tooltip>
                  </template>
                </Flightsheet>
              </el-col>
            </el-row>
            <el-row>
              <el-col>
                <h3>Available</h3>
              </el-col>
            </el-row>
            <el-row>
              <el-col>
                <Flightsheet :flightsheets="flightsheets.available" nonew nodelete>
                  <template #action="scope">
                    <el-tooltip effect="dark" content="Launch this flightsheet" placement="top">
                      <el-button type="success" :loading="flightsheets.launched" @click.stop="() => launchFlightsheet(scope.fs)"><span class="mdi mdi-rocket-launch"></span></el-button>
                    </el-tooltip>
                  </template>
                </Flightsheet>
              </el-col>
            </el-row>
          </el-tab-pane>
          <el-tab-pane class="rig-tabs-pane" label="Overclocking" name="overclocking">
            <el-row v-for="item in overclocking" :key="item">

            </el-row>
            <el-row justify="end">
              <el-col :span="3">
                <el-button @click="newOverclockingOpen = true">Configure new algorithm</el-button>
              </el-col>
            </el-row>
            <el-dialog v-model="newOverclockingOpen" title="New algorithm overclocking">

            </el-dialog>
          </el-tab-pane>
          <el-tab-pane class="rig-tabs-pane" label="Settings" name="settings">
            <el-container class="container" direction="vertical">
              <el-row>
                <el-col :span="12" :xs="24">
                  <h3>ID</h3>
                </el-col>
                <el-col :span="12" :xs="24" class="rig-settings-right-col">
                  <p>{{farm.workers.id}}</p>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="12" :xs="24">
                  <h3>Farm ID</h3>
                </el-col>
                <el-col :span="12" :xs="24">
                  <el-input v-model="farm._id.$oid"/>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="12" :xs="24">
                  <h3>Name</h3>
                </el-col>
                <el-col :span="12" :xs="24">
                  <el-input v-model="farm.workers.name"/>
                </el-col>
              </el-row>
              <el-row>
                <el-col class="rig-settings-right-col" :span="12" :xs="24" :offset="12">
                  <el-button type="primary" @click="update">Update worker</el-button>
                </el-col>
              </el-row>
              <el-divider/>
              <el-row>
                <el-col :span="12" :xs="24">
                  <h3>Password</h3>
                </el-col>
                <el-col :span="12" :xs="24">
                  <el-input type="password" v-model="farm.workers.pwd"/>
                </el-col>
              </el-row>
              <el-row>
                <el-col class="rig-settings-right-col" :span="12" :xs="24" :offset="12">
                  <el-button type="primary" @click="update">Update password</el-button>
                </el-col>
              </el-row>
              <el-divider/>
              <el-row>
                <el-col :span="12" :xs="24">
                  <h3>Remove worker</h3>
                  <small>Removed workers cannot be recovered</small>
                </el-col>
                <el-col class="rig-settings-right-col" :span="12" :xs="24">
                  <el-popconfirm title="Are you sure to remove this rig ?" @confirm="remove">
                    <template #reference>
                      <el-button type="danger" plain>Remove</el-button>
                    </template>
                  </el-popconfirm>
                </el-col>
              </el-row>
            </el-container>
          </el-tab-pane>
        </el-tabs>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import api from "../api/antos-api"
import Flightsheet from "@/components/Flightsheet";
import {ElNotification} from "element-plus";
import utils from "@/utils/utils";

export default {
  name: "Rig",
  components: {Flightsheet},
  data() {
    return {
      farm: {
        _id: {
          $oid: ""
        },
        workers: {}
      },
      loading: true,
      activeTab: 'overview',
      flightsheets: {
        available: [],
        inuse: [],
        launched: false
      },
      autofan: {
        criticalTemp: 90,
        criticalAction: 'stop',
        enabled: false,
        targetCoreTemp: 70,
        targetMemTemp: 90,
        minFanSpeed: 25,
        maxFanSpeed: 100,
        noAmd: false,
        smartMode: false,
        rebootOnError: false
      },
      applyAutofanLoading: false,
      overclocking: [],
      newOverclockingOpen: false,
      runCommandVisible: false,
      commandToRun: '',
      runCommandLoading: false
    }
  },
  mounted() {
    api.get_farm_worker(this.$route.params.id, this.$route.params.rig).then(resp => {
      this.farm = resp.data
      this.farm.workers = this.farm.workers[0]
      if(this.farm.workers.hardwares !== undefined) {
        this.farm.workers.hardwares.gpu = this.farm.workers.hardwares?.gpu.filter(value => value.brand !== 'cpu')
      }
      if(this.farm.workers.stats !== undefined) {
        this.farm.workers.stats.power = this.farm.workers.stats?.power.filter(value => value !== 0)
      }
      this.loading = false
    })
    this.downloadSortFlightsheets()
  },
  computed: {
    formatTimestamp() {
      const d = new Date(0);
      d.setUTCSeconds(this.farm.workers.hardwares?.boot_time)
      const now = new Date()
      const days = now.getDate() - d.getDate()
      if(days <= 0) {
        const hours = Math.round(Math.abs(now - d) / 36e5)
        if(hours === 0) {
          return "moment ago"
        }
        return hours + " hours ago"
      }
      return days + " days ago"
    },
    totalConsumption() {
      return this.farm.workers.stats?.power.reduce((old, next) => old + next)
    },
    isOnline() {
      return utils.isWorkerOnline(this.farm.workers)
    }
  },
  methods: {
    downloadSortFlightsheets() {
      api.get_farm_flightsheet(this.$route.params.id).then(resp => {
        this.flightsheets.available = resp.data
      }).then(() => {
        api.get_rig_config(this.$route.params.rig).then(resp => {
          if(resp.data !== null) {
            const inuseId = resp.data.flightsheet
            this.flightsheets.inuse = this.flightsheets.available.filter(value => value._id.$oid === inuseId)
            this.flightsheets.available = this.flightsheets.available.filter(value => value._id.$oid !== inuseId)
          }
          else {
            this.flightsheets.inuse = []
          }
        })
      })
    },
    reboot() {
      api.post_command("batch", this.$route.params.rig, "reboot").then(resp => {
        console.log(resp)
      })
    },
    shutdown() {
      api.post_command('batch', this.$route.params.rig, 'shutdown now').then(resp => {
        if(resp.status === 200) {
          ElNotification({
            title: 'Shutting down the rig...',
            type: 'success',
            message: 'The rig will shut down now'
          })
        }
      })
    },
    launchFlightsheet(sheet) {
      this.flightsheets.launched = true
      api.post_rig_config(this.$route.params.rig, sheet._id.$oid).then(resp => {
        if(resp.status === 200) {
          this.downloadSortFlightsheets()
          this.flightsheets.launched = false
          ElNotification({
            title: 'Configuration changed',
            type: 'success',
            message: 'Configuration sent. The rig will receive it soon.'
          })
          api.post_command("config", this.$route.params.rig)
        }
      })
    },
    stopFlightsheet() {
      this.flightsheets.launched = true
      api.delete_rig_config(this.$route.params.rig).then(resp => {
        if(resp.status === 200) {
          this.downloadSortFlightsheets()
          this.flightsheets.launched = false
          ElNotification({
            title: 'Configuration changed',
            type: 'warning',
            message: 'Configuration sent. The rig will receive it soon.'
          })
          api.post_command("config", this.$route.params.rig)
        }
      })
    },
    hashrateFromGPU(index) {
      index = this.isMotherboardGPUPresent ? index+1 : index
      let hs = this.farm.workers.stats?.miner_stats?.hs[index]
      return this.hasrateFromRaw(hs)
    },
    hasrateFromRaw(hs) {
      let suffix = this.farm.workers.stats?.miner_stats?.hs_units
      if(suffix === 'hs') {
        hs /= 1000000
      }
      else if(suffix === 'khs' || suffix === undefined) {
        hs /= 1000
      }
      suffix = 'MH/s'
      return hs?.toFixed(2) + ' ' + suffix
    },
    remove() {
      api.delete_rig(this.$route.params.rig).then(resp => {
        if(resp.status === 200) {
          this.$router.push('/farm/' + this.$route.params.id)
        }
      })
    },
    update() {
      api.put_miner(this.$route.params.rig, this.farm.workers.name, this.farm.workers.pwd).then(resp => {
        if(resp.status === 200) {
          ElNotification({
            title: 'Worker updated successfully',
            type: 'success',
            message: 'Worker has been updated successfully. It will sync the new changes now.'
          })
          api.post_command("config", this.$route.params.rig)
        }
      })
    },
    applyAutofan() {
      this.applyAutofanLoading = true
      api.post_rig_autofan(this.$route.params.rig, this.autofan).then(resp => {
        if(resp.status === 200) {
          this.applyAutofanLoading = false
          ElNotification({
            title: 'Autofan config changed !',
            message: 'Autofan config sent to the worker',
            type: 'success'
          })
          api.post_command("autofan", this.$route.params.rig)
        }
      })
    },
    runCommand() {
      this.runCommandLoading = true
      api.post_command("batch", this.$route.params.rig, this.commandToRun).then(resp => {
        if(resp.status === 200) {
          this.runCommandLoading = false
          this.commandToRun = ''
          this.runCommandVisible = false
        }
      })
    }
  }
}
</script>

<style scoped>
.gpu-table-name {
  font-size: 1.2rem;
  color: var(--el-color-warning);
}

.gpu-table-desc {
  font-size: 0.8rem;
  color: silver;
}

.rig-tabs-pane {
  margin-top: 50px;
}

h2 {
  font-size: 2rem;
}

.gray-bold {
  color: dimgray;
  font-weight: bold;
}

.gray-normal {
  color: slategray;
  font-weight: normal;
}

.rig-header {
  background-color: #F8F0DF;
  padding-top: 10px;
  padding-bottom: 10px;
}

.rig-commands-col {
  text-align: right;
}

.rig-col-cards {
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 10px;
  padding-bottom: 5px;
}

.rig-cards {
  height: 100%;
}

.rig-miner-version {
  color: white;
  font-size: 10px;
}

.rig-row-miner .rig-row-miner-title {

  background-color: #98AD9F;
  padding: 3px;
  color: white;
}

.rig-row-miner {
  background-color: #C0B8A8;
  padding: 10px;
}

.rig-row-algo-title {
  font-size: 17px;
  margin-top: 3px;
  color: seagreen;
  text-transform: uppercase;
}

.rig-row-algo-hs {
  font-weight: bold;
  color: whitesmoke;
}

.el-card {
  --el-card-padding: 0 5px!important;
}

.container {
  margin-left: 15%;
  margin-right: 15%;
}

.rig-settings-right-col {
  text-align: left;
}
</style>